*,
html,
body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif; }
p {
    margin-bottom: 0; }
ul {
    padding: 0;
    margin: 0; }
a,
a:hover,
a:active,
a:focus,
a:visited {
    color: #000;
    text-decoration: none; }

.h-vh {
    height: 100dvh; }


.container {
    width: 100%;
    max-width: 1300px;
    margin: 0 auto; }
ul {
    list-style: none; }


.header__block {
        display: flex;
        justify-content: space-around; }
.header__menu {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 40%;
        margin-bottom: 0; }

.carousel-item {
    max-height: 100dvh; }

.main_for_header {
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    position: fixed;
    z-index: 1000;
    height: 80px;
    background-color: rgba(255, 255, 255, 0.7); }


.div_for_center {
    max-width: 1200px;
    margin: 0 auto; }

.main {
    margin-top: 107px;
    &__h {
        color: #111;
        font-family: 'Montserrat', sans-serif;
        font-size: 63.488px;
        font-weight: 700;
        line-height: 69.837px /* 110% */;
        margin-bottom: 32px; }
    &__text {
        width: 500px;
        color: #111;
        font-family: 'Montserrat', sans-serif;
        font-size: 13.96px;
        font-weight: 400;
        line-height: 27.919px /* 200% */;
        margin-bottom: 54px; }
    &__flex {
        display: flex;
        justify-content: space-around; }

    &__tel {
        width: 400px;
        display: flex;
        justify-content: space-around;
        padding-bottom: 46px; }
    &__info_tel {
        color: #111;
        text-align: center;
        font-family: 'Montserrat', sans-serif;
        font-size: 30px;
        font-weight: 700;
        line-height: 57.6px; /* 160% */;
        letter-spacing: 1px; } }
.product {
    width: 100%;
    height: 157px;
    background: #56AC78;
    display: flex;
    flex-direction: column;
    justify-content: center;
    &__my {
        color: #FFF;
        text-align: center;
        font-family: 'Montserrat', sans-serif;
        font-size: 30px;
        font-weight: 700;
        line-height: 33px /* 110% */; } }
.products {
    background: url("../img/bg-main.png") lightgray 50% / cover no-repeat;
    &__button {
        display: flex;
        justify-content: space-around; }
    &__top_text {
        position: relative;
        top: 25px;
        padding-top: 10px;
        display: flex;
        justify-content: space-around; }
    &__mains {
        content: "";
        background: rgba(255, 255, 255, 0.70); }
    &__main {
        display: flex;
        justify-content: space-around;
        padding-bottom: 170px;
        align-items: stretch; }
    &__header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 40px 0; }
    &__h {
        color: #56AC78;
        text-align: center;
        font-family: 'Montserrat', sans-serif;
        font-size: 18px;
        font-weight: 400;
        line-height: 13.04px /* 110% */;
        margin-bottom: 20px; }
    &__image {
        margin: 0 auto; }
    &__image_p {
        width: 130px;
        height: 77px;
        border-radius: 10px;
        border: 1px solid #56AC78; }
    &__line {
        content: "";
        width: 1px;
        height: 48px;
        background: #56AC78;
        margin: 0 auto;
        transform: translateX(-50%);
        margin-bottom: 40px; }
    &__blocks {
        margin-bottom: 50px;
        &_img {
            width: 287px;
            height: 287px;
            border-radius: 311px;
            border: 12px solid #56AC78;
            overflow: hidden;
            -webkit-transition: all 0.5s ease;
            transition: all 0.5s ease;
            margin: 0 auto; }
        &_img:hover {
            -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }

        &_text {
            color: #111;
            font-family: 'Montserrat', sans-serif;
            font-size: 13.977px;
            font-weight: 500;
            line-height: 25px;
            margin-top: 35px;
            text-align: center; }
        &_black {
            color: #111;
            text-align: center;
            font-family: 'Montserrat', sans-serif;
            font-size: 13.977px;
            font-weight: 700;
            line-height: 25px /* 178.866% */; } } }

.image_one {
